html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video { margin: 0; padding: 0; border: 0; outline: 0; color: #444; 
 font-size: 12px; line-height: 1.4; vertical-align: baseline; } 

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section { display: block; } 

body { min-width: 320px; word-break: break-all; -webkit-text-size-adjust: none; } 

ol,
ul,
li { list-style: none; } 

blockquote,
q { quotes: none; } 

blockquote:before,
blockquote:after,
q:before,
q:after { content: ''; content: none; } 

table { border-collapse: collapse; border-spacing: 0; } 

img { vertical-align: top; } 

input[type=radio],
input[type=checkbox] { margin-top: -1px; vertical-align: top; } 

input[type=text],
input[type=email],
input[type=password] { vertical-align: top; } 

a,
a:hover,
a:active,
a:focus { text-decoration: none; } 

*,
*:before,
*:after { -webkit-box-sizing: border-box; -moz-box-sizing: border-box; box-sizing: border-box; } 

input[type=text],
input[type=password],
input[type=submit],
input[type=button],
input[type=search],
input[type=tel],
input[type=number],
input[type=email],
textarea { border-radius: 0; appearance: none; } 

/* 버튼스타일 - 기본 */
.box_btn {display:inline-block; text-align:center; vertical-align:top;}
.box_btn > * {display:inline-block; margin:0; padding:11px 12px; border:1px solid #676767; outline:none; background:#666; color:#fff !important; font-size:12px;  font-weight:normal; text-align:center; vertical-align:middle; line-height:1.4; white-space:nowrap; cursor:pointer; transition:all .2s ease;}
.box_btn > *:hover,
.box_btn > *:active,
.box_btn > *:focus {background:#333;}

/*-----------------------------------------------------------------*/
/* 2. Global
/*-----------------------------------------------------------------*/

.dn { display: none !important; } 

.hidden { visibility: hidden; overflow: hidden; width: 0 !important; height: 0 !important; margin: 0 !important; padding: 0 !important; font-size: 0; line-height: 0; } 

.tac { text-align: center !important; } 

.tal { text-align: left !important; } 

.tar { text-align: right !important; } 

.empty { padding: 20px 0 !important; text-align: center; } 

.fl { float: left !important; } 

.fr { float: right !important; } 

.clear { clear: both; } 

.bc { display: block; margin-left: auto; margin-right: auto; } 

.p_color { color: #d53838 !important; } 

/* input 기본 스타일 */
.form_input { height: 34px; padding: 4px 10px; border: 1px solid #dcdcdc; background: #fff; color: #666; 
 font-size: 1rem; transition: all .3s ease; } 

.form_input:focus { border-color: #000; outline: none !important; } 

.form_input.block { width: 100% !important; } 

.form_input:-ms-input-placeholder { color: #666; }

.form_input::-webkit-input-placeholder { color: #666; }

.form_input::-moz-placeholder { color: #666; }

.form_input.readonly { background: #e0e0e0 !important; } 

.form_input.gray { background: #fafafa; } 

.form_input.gray:focus { background: #fff; } 

textarea.form_input { height: auto; } 


/* css blog */
:root { /* color */
 --color-black:#000; } 
.App { text-align: center; } 

.App .blog_wrap { display:block; width:500px; height:auto; margin:150px auto 0; padding:15px; border:1px solid var(--color-black); text-align:left; } 
.App .blog_wrap > h1 { padding-bottom:15px; border-bottom:1px solid var(--color-black); font-size:20px; } 
.App .blog_wrap .top_section { position:relative; height:30px; border-bottom:1px solid var(--color-black); } 
.App .blog_wrap .top_section > span { position:absolute; top:0; right:0; line-height:30px; cursor:pointer; } 
.App .blog_wrap .blog_box { position:relative; padding:10px 0; border-bottom:1px solid #aaa; } 
.App .blog_wrap .blog_box > h3 { position:relative; width:calc(100% - 50px); font-size:15px; cursor:pointer; } 
.App .blog_wrap .blog_box > h3 .title_edit { padding-left:10px; font-size:13px; vertical-align:middle; cursor:pointer; } 
.App .blog_wrap .blog_box .like_btn { position:absolute; top:4px; right:10px; font-size:14px; cursor:pointer; } 
.App .blog_wrap .blog_box .like_btn span { padding-left:5px; } 
.App .blog_wrap .blog_box > p { padding-top:5px; } 
.App .blog_wrap .del {position:absolute; bottom:10px; right:10px; cursor:pointer;}
.App .blog_wrap .input_section {margin-top:50px; text-align:center;}
.App .blog_wrap .input_section .box_btn {margin-left:5px;}
.App .blog_wrap .input_section .box_btn > * {height:34px; padding:0 10px; line-height:34px;}

.App .blog_modal { position:relative; width:500px; margin:20px auto 0; padding:15px; background:#eee; text-align:left; } 
.App .blog_modal > h4 { padding-bottom:10px; font-size:15px; } 
.App .blog_modal > p.modal_date { color:#333; font-size:12px; } 
.App .blog_modal > p.modal_cont { padding-top:20px; font-size:14px; } 
.App .blog_modal .modal_edit { position:absolute; top:15px; right:15px; padding:5px; border:1px solid #333; border-radius:5px; cursor:pointer; } 


.App-logo { height: 40vmin; pointer-events: none; } 

@media (prefers-reduced-motion: no-preference){
 .App-logo { animation: App-logo-spin infinite 20s linear; } 
 }

.App-header { background-color: #282c34; min-height: 100vh; display: flex; flex-direction: column; align-items: center; justify-content: center; font-size: calc(10px + 2vmin); color: white; } 

.App-link { color: #61dafb; } 

@keyframes App-logo-spin { 
 from { transform: rotate(0deg); } 
 to { transform: rotate(360deg); } 
 }
